.new-container {
  display: flex;
  height: 40vh;
  align-items: center;
  justify-content: center;
  background-color: #fcf5f5;
  flex-direction: column;
}

.new-title {
  font-size: 70px;
  margin-bottom: 20px;
}
.new-des {
  font-size: 30px;
  letter-spacing: 5px;
  font-weight: 300;
  margin-bottom: 20px;
}
.input-container {
  width: 50%;
  height: 40px;

  background-color: white;
  display: flex;
  justify-content: space-between;
  border: 1px solid lightgray;
}
.input {
  border: none;
  flex: 8;
  padding-left: 20px;
}
.new-btn {
  flex: 1;
  cursor: pointer;
  border: none;
  background-color: teal;
  color: white;
}
@media only screen and (max-width: 500px) {
  .new-container{
    height: 30vh;
    display: flex;
    
    flex-direction: column;
    
    
  }
  .new-title {
    font-size: 30px;
    margin-bottom: 15px;
  }
  .new-des{
    font-size: 20px;
    text-align: center;

  }
  
}
